label {
  text-align: center;
}

.custom__checkbox {
  margin-top: 20px;
  position: relative;
  z-index: 1;
  opacity: 1;
}

.custom__checkbox {
  display: inline-flex;
  align-items: center;
  user-select: none;
}

.custom__checkbox {
  content: "";
  display: inline-block;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #adb5bd;
  border-radius: 0.25em;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}

.custom__checkbox:checked {
  border-color: #0b76ef;
  background-color: #0b76ef;
}

/* стили при наведении курсора на checkbox */
.custom__checkbox:not(:disabled):not(:checked):hover {
  border-color: #086bdb;
}
/* стили для активного состояния чекбокса (при нажатии на него) */
.custom__checkbox:not(:disabled):active {
  background-color: #086bdb;
  border-color: #086bdb;
}
/* стили для чекбокса, находящегося в фокусе */
.custom__checkbox:focus {
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}
/* стили для чекбокса, находящегося в фокусе и не находящегося в состоянии checked */
.custom__checkbox:focus:not(:checked) {
  border-color: #086bdb;
}
/* стили для чекбокса, находящегося в состоянии disabled */
.custom__checkbox:disabled {
  background-color: #086bdb;
}
